.navigation-bar {
  background: rgb(93, 31, 238);
  background: linear-gradient(90deg, #f75964 0%, #f99468 100%);
  position: fixed;
  left: 0;
  top: 0;
  width: 5rem;
  height: 100vh;
  transition: width 0.6s ease;
  color: white;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.15);

  &:hover {
    width: 14rem;
    .expand-button {
      span {
        display: block;
      }
      .MuiSvgIcon-root {
        transform: rotate(-180deg);
      }
    }
  }

  @media only screen and (max-width: 680px) {
    top: auto;
    bottom: 0;
    width: 100%;
    height: 4rem;

    &:hover {
      width: 100%;
    }
  }
  .expand-button {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px 0;
    background: rgba(255, 255, 255, 0.15);

    @media only screen and (max-width: 680px) {
      display: none;
    }

    span {
      display: none;
      margin: 0 10px;
      font-weight: 800;
      min-width: 120px;
    }

    .MuiSvgIcon-root {
      transition: transform 0.4s ease;
      font-size: 35px;
    }
  }

  .links {
    display: flex;
    flex-direction: column;
    justify-content: center;

    @media only screen and (max-width: 680px) {
      flex-direction: row;
      height: 100%;
    }

    .link-item {
      display: flex;
      align-items: center;
      padding: 10px 0;
      margin: 12px 0;
      overflow: hidden;
      text-decoration: none;
      color: #ffffff;
      border-bottom: 1px solid rgba(255, 255, 255, 0.15);

      &.active-nav-link {
        position: relative;

        &::before {
          position: absolute;
          content: "";
          width: 100%;
          height: 100%;
          background: rgba(255, 255, 255, 0.2);
          border-left: 3px solid rgba(27, 238, 38, 1);
          border-top: 1px solid rgba(255, 255, 255, 0.15);

          @media only screen and (max-width: 680px) {
            border-bottom: 3px solid rgba(27, 238, 38, 1);
            border-left: none;
          }
        }
      }

      &:hover {
        // background: rgba(255, 255, 255, 0.1);
        cursor: pointer;
      }

      @media only screen and (max-width: 680px) {
        flex-direction: column;
        justify-content: space-around;
        width: 100%;
        margin: 0;
      }

      .MuiSvgIcon-root {
        margin: 0 24px;

        @media only screen and (max-width: 680px) {
          margin: 0;
        }
      }

      span {
        margin-left: 8px;
        min-width: 200px;

        @media only screen and (max-width: 680px) {
          margin: 0;
          margin-top: 4px;
          min-width: fit-content;
        }
      }
    }
  }
}
